<template>
  <div id="app" ref="app"></div>
</template>
<script>
import * as THREE from "three";
import OrbitControls from "three-orbitcontrols";
import Stats from "three/examples/jsm/libs/stats.module.js";
import { GUI } from "three/examples/jsm/libs/dat.gui.module.js";
import { Sky } from "three/examples/jsm/objects/Sky.js";
import { Water } from "three/examples/jsm/objects/Water.js";
export default {
  data() {
    return {
      container: null,
      camera: null,
      scene: null,
      sphere: null,
      stats: null,
      sky: null,
      light: null,
      renderer: null
    };
  },
  mounted() {
    this.init();
    // this.animate();
  },
  methods: {
    init() {
      //初始化场景
      const scene = new THREE.Scene();
      //初始化容器
      const container = this.$refs.app;
      //初始化渲染器
      const renderer = new THREE.WebGLRenderer();
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.setSize(window.innerWidth, window.innerHeight);
      container.appendChild(renderer.domElement);
      //初始化相机
      const camera = new THREE.PerspectiveCamera(
        55,
        window.innerWidth / window.innerHeight,
        1,
        20000
      );
      //设置相机位置
      camera.position.set(30, 50, 100);
      //初始化控制器
      const controls = new OrbitControls(camera, renderer.domElement);
      controls.update();
      //初始化灯光
      let light = new THREE.DirectionalLight(0xffffff, 0.8);
      scene.add(light);
      //water
      var waterGeometry = new THREE.PlaneBufferGeometry(10000, 10000);
      let water = new Water(waterGeometry, {
        textureWidth: 512,
        textureHeight: 512,
        waterNormals: new THREE.TextureLoader().load(
          require("./assets/Water_1_M_Normal.jpg"),
          function(texture) {
            texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
          }
        ),
        alpha: 1.0,
        sunDirection: light.position.clone().normalize(),
        sunColor: 0xffffff,
        waterColor: 0x001e0f,
        distortionScale: 3.7,
        fog: scene.fog !== undefined
      });
      water.rotation.x = -Math.PI / 2;
      scene.add(water);
      //初始化天空
      var sky = new Sky();
      var uniforms = sky.material.uniforms;
      uniforms["turbidity"].value = 10;
      uniforms["rayleigh"].value = 2;
      uniforms["luminance"].value = 1;
      uniforms["mieCoefficient"].value = 0.005;
      uniforms["mieDirectionalG"].value = 0.8;
      var parameters = {
        distance: 400,
        inclination: 0.49,
        azimuth: 0.205
      };
      var cubeCamera = new THREE.CubeCamera(0.1, 1, 512);
      cubeCamera.renderTarget.texture.generateMipmaps = true;
      cubeCamera.renderTarget.texture.minFilter =
        THREE.LinearMipMapLinearFilter;
      scene.background = cubeCamera.renderTarget;
      //sun
      function updateSun() {
        var theta = Math.PI * (parameters.inclination - 0.5);
        var phi = 2 * Math.PI * (parameters.azimuth - 0.5);
        light.position.x = parameters.distance * Math.cos(phi);
        light.position.y =
          parameters.distance * Math.sin(phi) * Math.sin(theta);
        light.position.z =
          parameters.distance * Math.sin(phi) * Math.cos(theta);
        sky.material.uniforms["sunPosition"].value = light.position.copy(
          light.position
        );
        water.material.uniforms["sunDirection"].value
          .copy(light.position)
          .normalize();
        cubeCamera.update(renderer, sky);
      }
      updateSun();
      //

      var geometry = new THREE.IcosahedronBufferGeometry(20, 1);
      var count = geometry.attributes.position.count;
      var colors = [];
      var color = new THREE.Color();
      for (var i = 0; i < count; i += 3) {
        color.setHex(Math.random() * 0xffffff);
        colors.push(color.r, color.g, color.b);
        colors.push(color.r, color.g, color.b);
        colors.push(color.r, color.g, color.b);
      }
      geometry.addAttribute(
        "color",
        new THREE.Float32BufferAttribute(colors, 3)
      );
      var material = new THREE.MeshStandardMaterial({
        vertexColors: THREE.VertexColors,
        roughness: 0.0,
        flatShading: true,
        envMap: cubeCamera.renderTarget.texture,
        side: THREE.DoubleSide
      });
      let sphere = new THREE.Mesh(geometry, material);
      // scene.add(sphere);
      //
      // let stats = new Stats();
      // container.appendChild(stats.dom);

      //gui
      // var gui = new GUI();
      // var folder = gui.addFolder("Sky");
      // folder.add(parameters, "inclination", 0, 0.5, 0.0001).onChange(updateSun);
      // folder.add(parameters, "azimuth", 0, 1, 0.0001).onChange(updateSun);
      // folder.open();
      // var uniforms = water.material.uniforms;
      // var folder = gui.addFolder("Water");
      // folder
      //   .add(uniforms.distortionScale, "value", 0, 8, 0.1)
      //   .name("distortionScale");
      // folder.add(uniforms.size, "value", 0.1, 10, 0.1).name("size");
      // folder.add(uniforms.alpha, "value", 0.9, 1, 0.001).name("alpha");
      // folder.open();
      function render() {
        var time = performance.now() * 0.001;
        sphere.position.y = Math.sin(time) * 20 + 5;
        sphere.rotation.x = time * 0.5;
        sphere.rotation.z = time * 0.51;
        water.material.uniforms["time"].value += 1.0 / 60.0;
        renderer.render(scene, camera);
      }
      renderer.render(scene, camera);
      function animate() {
        requestAnimationFrame(animate);
        // stats.update();
        controls.update();
        render();
        // renderer.render(scene, camera);
      }
      animate();
    }
  }
};
</script>
